    showHtmlBg() {
        if (this._htmlBG) {
            this._htmlBG.style.visibility = 'visible'
        }
    }
    hideHtmlBg() {
        if (this._htmlBG) {
            this._htmlBG.style.visibility = 'hidden'
        }
    }

    showHtmlNextBtn(clickCallback: () => void) {
        if (window.location.hostname == 'localhost') {
            let loadBtnImage = (cb: (src: string) => void) => {
                resources.load('product/PrimeFreeze/image/btn_next_2', (err, data: ImageAsset) => {
                    const currentSrc = data.data['currentSrc']
                    cb(currentSrc)
                })
            }

            let imgEle = window.document.getElementById('NextBtn')
            if (!imgEle) {
                loadBtnImage((currentSrc) => {
                    console.log(`currentSrc: ${currentSrc}`)
                    const gameDiv = window.document.getElementById('GameDiv')
                    const gameDivWidth = Number(gameDiv.style.width.split('px')[0])
                    console.log(`gameDivWidth: ${gameDivWidth}`)
                    const scale = gameDivWidth / 1540
                    const imgEle = window.document.createElement('img')
                    imgEle.id = 'NextBtn'
                    imgEle.src = currentSrc
                    imgEle.style.position = 'absolute'
                    imgEle.width = 88 * scale
                    imgEle.height = 88 * scale
                    imgEle.style.right = '10px'
                    imgEle.style.bottom = '10px'
                    imgEle.style.zIndex = '1000'
                    gameDiv.appendChild(imgEle)
                    imgEle.onclick = clickCallback
                })
            } else {
                imgEle.style.visibility = 'visible'
                imgEle.onclick = clickCallback
            }
            return
        }

        if (this._htmlNextBtn) {
            this._htmlNextBtn.style.visibility = 'visible'
            this._htmlNextBtn.onclick = clickCallback
        }
    }

    hideHtmlNextBtn() {
        let imgEle = window.document.getElementById('NextBtn')
        if (imgEle) {
            imgEle.style.visibility = 'hidden'
            imgEle.onclick = null
        } else {
            this._htmlNextBtn.style.visibility = 'hidden'
            this._htmlNextBtn.onclick = null
        }
    }

    hideGame() {
        if (this._gameFrame) {
            this._gameFrame.style.visibility = 'hidden'
        }
    }
    showGame() {
        if (this._gameFrame) {
            this._gameFrame.style.visibility = 'visible'
        }
    }

    showHtmlBackBtn(clickCallback: () => void) {
        if (window.location.hostname == 'localhost') {
            return
        }

        if (this._htmlBackBtn) {
            this._htmlBackBtn.style.visibility = 'visible'
            this._htmlBackBtn.onclick = clickCallback
        }
    }

    hideHtmlBackBtn() {
        if (this._htmlBackBtn) {
            this._htmlBackBtn.style.visibility = 'hidden'
            this._htmlBackBtn.onclick = null
        }
    }
