import { EventDispatcher } from '../../event/EventDispatcher'
import { MaouEvent } from '../../event/MaouEvent'
import LoadEvent from '../events/LoadEvent'
import TaskEvent from '../events/TaskEvent'
import { eLoaderStatus } from './EnumLoader'
import LoaderVO from './LoaderVO'

/**
 * 
 */
export default class LoadTask extends EventDispatcher {
    private static _loadTaskPool: Array<LoadTask> = []
    private static _idCounter = 0

    public id: number
    public thisObj: object = null
    public onCompleteHandler: () => void = null
    public onProgressHandler: (p: number) => void = null
    public onErrorHandler: (err: Error) => void = null

    private _voList: Array<LoaderVO> = []
    private _status: eLoaderStatus = eLoaderStatus.READY

    protected dispose(): void {
        let vo: LoaderVO
        for (vo of this._voList) {
            this._removeEvent(vo)
            if (vo.status != eLoaderStatus.DISPOSE) {
                vo.counter -= 1
            } else {
                console.warn('LoaderVO is disposed!')
            }
        }
        this.thisObj = this.onCompleteHandler = this.onProgressHandler = this.onErrorHandler = null
        this._voList.length = 0
        this._status = eLoaderStatus.DISPOSE
        this.removeAll()
        console.log(`task:${this.id} dispose`)
        this.id = -1
    }

    /** TaskEvent.FINISH */
    public cancel(): void {
        console.log(`task:${this.id} cancel`)
        this._status = eLoaderStatus.CANCLE
        let vo: LoaderVO
        for (vo of this._voList) {
            this._removeEvent(vo)
        }
        this.checkThenComplete()
    }

    /**  */
    public attachToLoaderVO(vo: LoaderVO): void {
        if (this._voList.indexOf(vo) == -1) {
            vo.counter += 1
            this._voList.push(vo)
            this._addEvent(vo)
        } else {
            console.warn('LoaderVO has already in LoadTask')
        }
    }

    public checkThenComplete(): boolean {
        let isComplete: boolean = this._canComplete()

        if (isComplete) {
            console.log(`task:${this.id} complete`)

            this.onProgressHandler && this.onProgressHandler.apply(this.thisObj, [1])
            this.onCompleteHandler && this.onCompleteHandler.apply(this.thisObj)

            this._finish()
        }
        // 
        else if (this._status == eLoaderStatus.CANCLE) {
            console.log(`task:${this.id} cancel`)
            this._finish()
        }
        // 
        else if (this._hasFail()) {
            console.log(`task:${this.id} fail`)

            const failVO = this._getFailVO()
            this.onErrorHandler && this.onErrorHandler.apply(this.thisObj, [failVO.error])

            this._finish()
        }

        return isComplete
    }

    /**  LoaderVO  */
    private _addEvent(vo: LoaderVO): void {
        vo.addEventListener(LoadEvent.PROGRESS, this.onLoadProgress, this)

        /** LoaderVO  */
        vo.addEventListener(
            LoadEvent.CHECK_TASK,
            () => {
                this.checkThenComplete()
            },
            this
        )
    }

    /**  LoaderVO  */
    private _removeEvent(vo: LoaderVO): void {
        vo.removeTargetListener(this)
    }

    /** LoaderVO  */
    protected onLoadProgress(evt: LoadEvent): void {
        this._status = eLoaderStatus.LOADING
        if (this.onProgressHandler) {
            let progress: number = this.calculateProgress()
            this.onProgressHandler.apply(this.thisObj, [progress])
        }
    }

    private calculateProgress(): number {
        let ret: number = 0

        if (this._voList.length) {
            let sum: number = 0
            let vo: LoaderVO
            for (vo of this._voList) {
                sum += vo.progress
            }

            ret = sum / this._voList.length
        }

        return ret
    }

    // public get voList(): Array<LoaderVO> {
    //     return this._voList
    // }

    /**  Task  */
    private _canComplete(): boolean {
        // false
        if (this._status == eLoaderStatus.DISPOSE || this._status == eLoaderStatus.CANCLE) {
            return false
        }

        let isComplete: boolean = true

        let vo: LoaderVO
        for (vo of this._voList) {
            if (vo.status != eLoaderStatus.COMPLETED) {
                isComplete = false
                break
            }
        }

        return isComplete
    }

    /**  */
    private _hasFail(): boolean {
        let vo: LoaderVO
        for (vo of this._voList) {
            if (vo.status == eLoaderStatus.FAIL) {
                return true
            }
        }
        return false
    }

    private _getFailVO(): LoaderVO {
        let vo: LoaderVO
        for (vo of this._voList) {
            if (vo.status == eLoaderStatus.FAIL) {
                return vo
            }
        }
        return null
    }

    private _finish() {
        // 
        this.dispatchEvent(MaouEvent.Create(TaskEvent, TaskEvent.FINISH))
    }

    // --------------- LoadTask  start --------------- //
    public static Create(thisObj?: object, onCompleteFun?: () => void, onProgressFun?: (p: number) => void, onErrorFun?: (err: Error) => void): LoadTask {
        let task: LoadTask
        if (this._loadTaskPool.length) {
            task = this._loadTaskPool.shift()
        } else {
            task = new LoadTask()
        }
        task.thisObj = thisObj
        task.onCompleteHandler = onCompleteFun
        task.onProgressHandler = onProgressFun
        task.onErrorHandler = onErrorFun
        task._voList.length = 0
        task._status = eLoaderStatus.READY
        task.id = ++this._idCounter
        console.log(`task:${task.id} create`)
        return task
    }

    public static Recycle(task: LoadTask): void {
        if (this._loadTaskPool.indexOf(task) == -1) {
            this._loadTaskPool.push(task)
            task.dispose()
        }
    }
    // --------------- LoadTask  end --------------- //
}
