/**
 * <p><code>Handler</code> </p>
 * <p> Handler.create()  Handler  Handler.recover() </p>
 * <p><b></b></p>
 */
export default class Handler {
    /* [DISABLE-ADD-VARIABLE-DEFAULT-VALUE]*/

    /** @private handler*/
    private static _pool: Handler[] = []

    /** @private */
    private static _gid: number = 1

    /** (this)*/
    public caller: any

    /** */
    public method: Function

    /** */
    public args: any[]

    /** truerecover()false */
    public once: boolean = false

    protected _id: number = 0

    /**
     *  <code>Handler</code> 
     * @param	caller 
     * @param	method 
     * @param	args 
     * @param	once 
     */
    constructor(caller: any = null, method: Function = null, args: any[] = null, once: boolean = false) {
        this.setTo(caller, method, args, once)
    }

    /**
     * 
     * @param	caller (this)
     * @param	method 
     * @param	args 
     * @param	once truerecover()
     * @return   handler 
     */
    public setTo(caller: any, method: Function, args: any[], once: boolean): Handler {
        this._id = Handler._gid++
        this.caller = caller
        this.method = method
        this.args = args
        this.once = once
        return this
    }

    /**
     * 
     */
    public run(): any {
        if (this.method == null) {
            return null
        }
        if (this._id == 0) {
            throw new Error('Handler')
        }
        let id: number = this._id
        let result: any = this.method.apply(this.caller, this.args)
        this._id === id && this.once && this.recover()
        return result
    }

    /**
     * 
     * @param	data Array()
     */
    public runWith(data: any): any {
        if (this.method == null) {
            return null
        }
        if (this._id == 0) {
            throw new Error('Handler')
        }
        let id: number = this._id
        let result: any

        if (data == null) {
            result = this.method.apply(this.caller, this.args)
        } else if (!this.args && !(data instanceof Array)) {
            /* [IF-FLASH]*/
            result = this.method.call(this.caller, data)
        }
        // [IF-JS] else if (!args && !data.unshift) result= method.call(caller, data);
        else if (this.args) {
            result = this.method.apply(this.caller, this.args.concat(data))
        } else {
            result = this.method.apply(this.caller, data)
        }
        this._id === id && this.once && this.recover()
        return result
    }

    /**
     * 
     */
    public clear(): Handler {
        this.caller = null
        this.method = null
        this.args = null
        return this
    }

    /**
     *  Handler 
     */
    public recover(): void {
        if (this._id > 0) {
            this._id = 0
            Handler._pool.push(this.clear())
        }
    }

    /**
     * Handleroncefalse
     * @param	caller (this)
     * @param	method 
     * @param	args 
     * @param	once truerecover()true
     * @return  handler
     */
    public static create(caller: any, method: Function, args: Array<any> = null, once: boolean = true): Handler {
        if (Handler._pool.length) {
            return Handler._pool.shift().setTo(caller, method, args, once)
        }
        return new Handler(caller, method, args, once)
    }

    /** @private */
    public get id(): number {
        return this._id
    }
}
