export class BaseSectionVO {
    id: string = ''
    path: string = ''
    resource: string[] = []
    itemId: string = ''
}

export class VideoSectionVO extends BaseSectionVO {
    videoPath: string = ''
}

export class BoardSectionVO extends BaseSectionVO {
    items: {
        id: string
        icon: string
        iconDisable: string
        bigImage: string
        dragConf?: {
            size: { width: number; height: number }
            showBg: boolean
        }
    }[] = []
}

export class EndingSectionVO extends BaseSectionVO {
    videoUrls: string[] = []
    boardSectionId: string = ''
    items: string[] = []
}
