import { assetManager, resources, SpriteFrame } from 'cc'
import BaseCache from './BaseCache'

export default class SpriteFrameCache extends BaseCache {
    public get data(): SpriteFrame {
        return this._data as SpriteFrame
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof SpriteFrame) {
            resources.release(this._finalURL, SpriteFrame)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
