import { Node, resources, sp } from 'cc'
import BaseCache from './BaseCache'

export default class SkeletonDataCache extends BaseCache {
    public get data(): sp.SkeletonData {
        return this._data as sp.SkeletonData
    }

    public createNode(): Node {
        if (!this.data) {
            console.error('[SkeletonDataCache][createNode] dataNode')
            return null
        }
        const node = new Node()
        node.addComponent(sp.Skeleton).skeletonData = this.data
        node.layer = 1 << 25 // UI_2D
        return node
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof sp.SkeletonData) {
            resources.release(this._finalURL, sp.SkeletonData)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
