import { resources, SpriteAtlas, SpriteFrame } from 'cc'
import BaseCache from './BaseCache'

export default class AtlasCache extends BaseCache {
    public get data(): SpriteAtlas {
        return this._data as SpriteAtlas
    }

    public getSpriteFrame(name: string): SpriteFrame {
        return this.data ? this.data.getSpriteFrame(name) : null
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof SpriteAtlas) {
            resources.release(this._finalURL, SpriteAtlas)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
