import { Component, _decorator } from 'cc'
import { AssetManager } from '../asset/AssetManager'
import BaseCache from '../asset/cache/BaseCache'
const { ccclass } = _decorator

@ccclass('MaouController')
export default class MaouController extends Component {
    protected load(name: string, cb: (err: Error, cache: BaseCache) => void): void {
        const url = `Controllers/${this['__classname__']}/${name}`
        AssetManager.me.load(
            url,
            this,
            () => {
                const cache = AssetManager.me.cache.getRes(url)
                cb.call(this, [null, cache])
            },
            (err) => {
                cb.call(this, [err])
            }
        )
    }

    protected loadP(name: string): Promise<BaseCache> {
        return new Promise((resolve, reject) => {
            this.load(name, (err, cache) => {
                if (err != null) {
                    return reject(err)
                }
                return resolve(cache)
            })
        })
    }

    onLoad() {}
}
