import { resources, SpriteFrame } from 'cc'
import { ILoader } from './ILoader'
import LoaderVO from './LoaderVO'

/*
 * @Author: Blues
 * @Date: 2021-11-21 13:34:38
 * @LastEditTime: 2022-01-07 19:17:34
 * @LastEditors: Blues
 * @Description:
 */
export default class ImageLoader implements ILoader {
    load(vo: LoaderVO) {
        vo.finalLoadURL = vo.path + '/' + 'spriteFrame'
        resources.load(
            vo.finalLoadURL,
            SpriteFrame,
            (finished, total) => {
                vo.setProgress(finished / total)
            },
            (err, content) => {
                if (err != null) {
                    console.error('[imageLoader]', err)
                    vo.fail(err)
                } else {
                    vo.complete(content)
                }
            }
        )
    }
}
