import { AssetManager } from '../asset/AssetManager'
import { EventDispatcher } from '../event/EventDispatcher'
import { MaouEvent } from '../event/MaouEvent'
import { classname } from '../util/Decorators'
import { MaouCore } from './MaouCore'

export enum CommandState {
    None = 0,
    Load = 1,
    Ready = 2,
    Progreess = 3,
    Finish = 4
}

@classname('CommandEvent')
export class CommandEvent extends MaouEvent {
    public static FINISH = 'FINISH'
    public static ERROR = 'ERROR'
}

export default class MaouCommand extends EventDispatcher {
    state: CommandState = CommandState.None

    call() {
        MaouCore.Call(this)
    }

    finish() {
        this.dispatchEvent(MaouEvent.Create(CommandEvent, CommandEvent.FINISH))
    }

    error() {
        this.dispatchEvent(MaouEvent.Create(CommandEvent, CommandEvent.ERROR))
    }

    getController(): typeof MaouController {
        throw Error('getController need override!!!')
    }

    getResources(): Array<string> {
        return new Array<string>(0)
    }

    /**
     * 
     */
    getName() {
        const name = this.constructor['__NAME__']
        if (!name) {
            throw Error('classname()command')
        }
        return name
    }

    /**
     *  & 
     */
    async check() {
        return new Promise<boolean>((resolve, reject) => {
            // if (this.state !== CommandState.None) {
            //     console.warn(`[MaouCommand.check][Command(${this.getName()})] state != CommandState.None`)
            //     return resolve(false)
            // }
            // console.log(`[MaouCommand.check][Command(${this.getName()})]`)

            let paths = this.getResources()
            if (paths.length === 0) {
                this.state = CommandState.Ready
                return resolve(true)
            }

            this.state = CommandState.Load
            // TODO  Loading
            let count = 0
            AssetManager.me.loadGroup(
                paths,
                this,
                () => {
                    this.state = CommandState.Ready
                    // TODO  Loading
                    return resolve(true)
                },
                (err) => {
                    return resolve(false)
                },
                (progress) => {
                    this.state = CommandState.Progreess
                }
            )
        })
    }
}
