import { VideoPlayer, _decorator } from 'cc'
import { VideoSectionVO } from '../data/SectionVO'
import { GameManager } from '../manager/GameManager'
import { AssetManager } from '../maou/asset/AssetManager'
import VideoCache from '../maou/asset/cache/VideoCache'
// import { VideoSectionVO } from '../data/SectionVO'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('VideoSectionComponent')
export class VideoSectionComponent extends BaseSectionComponent {
    @property(VideoPlayer)
    videoPlayer: VideoPlayer = null

    // private _videoCache: VideoCache = null

    get data(): VideoSectionVO {
        return this._vo as VideoSectionVO
    }

    onLoaded() {
        // this._videoCache = AssetManager.me.cache.getVideo(this.data.resource[0])
        this.videoPlayer.remoteURL = this.data.videoPath
    }

    run() {
        // this.videoPlayer.clip = this._videoCache.data
        this.videoPlayer.play()
        const self = this
        GameManager.me.showHtmlNextBtn(() => {
            GameManager.me.hideHtmlNextBtn()
            self.clickNext()
        })
    }

    dispose() {
        // if (this._videoCache) {
        //     this._videoCache.dispose()
        //     this._videoCache = null
        // }
    }

    onVideoPlayerEvent(player, status) {
        console.log(status)
        // if (status == VideoPlayer.EventType.READY_TO_PLAY || status == VideoPlayer.EventType.COMPLETED) {
        //     player.play()
        // }
        if (status == VideoPlayer.EventType.READY_TO_PLAY) {
            player.play()
        }
    }
}
