import { resources, SpriteFrame, VideoClip } from 'cc'
import { ILoader } from './ILoader'
import LoaderVO from './LoaderVO'

export default class VideoLoader implements ILoader {
    load(vo: LoaderVO) {
        vo.finalLoadURL = vo.path
        resources.load(
            vo.finalLoadURL,
            VideoClip,
            (finished, total) => {
                vo.setProgress(finished / total)
            },
            (err, content) => {
                if (err != null) {
                    console.error('[videoLoader]', err)
                    vo.fail(err)
                } else {
                    vo.complete(content)
                }
            }
        )
    }
}
