import { AssetManager } from '../AssetManager'

export default class BaseCache {
    protected _url: string = null
    protected _finalURL: string = null
    protected _data: any = null
    protected _ref: number = 0 // 
    protected _isDisposed: boolean
    /**  */
    public lifeTime: number = 0

    constructor(url: string, finalURL: string, data: any) {
        this._url = url
        this._finalURL = finalURL
        this._data = data
    }

    get data() {
        return this._data
    }

    get url() {
        return this._url
    }

    get finalURL() {
        return this._finalURL
    }

    get isDisposed() {
        return this._isDisposed
    }

    /**
     * 1
     */
    getOnce(): any {
        return this._data
    }

    /**
     * 
     *
     */
    public dispose(): void {}

    protected addRef() {
        this._ref++
    }

    protected decRef() {
        this._ref--
    }

    /**
     * 
     *
     */
    public destroy(): void {
        this._data = null
        this._isDisposed = true
    }

    get refCount(): number {
        return this._ref
    }
}

export function CacheWarp<T extends BaseCache>(target: T): T {
    let getOnce = target.getOnce
    target.getOnce = function (): any {
        let result = getOnce.apply(this, arguments)
        this.addRef()
        return result
    }

    let dispose = target.dispose
    target.dispose = function (): void {
        let result = dispose.apply(this, arguments)
        this.decRef()
        if (this.refCount == 0) {
            // 0
            AssetManager.me.cache.setInertia(this)
        }
        return result
    }
    return target as T
}
