import { Button, Node, _decorator } from 'cc'
import { BoardSectionVO, EndingSectionVO } from '../data/SectionVO'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('EndingSectionComponent')
export class EndingSectionComponent extends BaseSectionComponent {
    @property(Node)
    btnBack: Node = null

    @property(Node)
    btn1: Node = null

    @property(Node)
    btn2: Node = null

    @property(Node)
    btn3: Node = null

    @property(Node)
    image1: Node = null

    @property(Node)
    image2: Node = null

    @property(Node)
    image3: Node = null

    get data(): EndingSectionVO {
        return this._vo as EndingSectionVO
    }

    onLoaded() {
        this.btnBack.active = false
        let count = 0
        const map = this.valueManager.get(this.data.boardSectionId)
        for (let i = 0; i < 3; i++) {
            const image = this[`image${i + 1}`] as Node
            if (image) {
                image.active = i == this.getItemIndex()
            }

            const btn = this[`btn${i + 1}`] as Node
            const key = this.data.items[i]
            if (key) {
                const flag = map[key]
                if (flag) {
                    btn && (btn.active = false)
                } else {
                    btn && (btn.active = true)
                    count++
                }
            } else {
                btn && (btn.active = false)
            }
        }
        this.btnBack.active = count == 0
    }

    dispose() {}

    clickWatchVideo() {
        const url = this.data.videoUrls[this.getItemIndex()]
        window.open(url)
    }

    private getItemIndex() {
        const sectionVO = this.sectionManager.getSection(this.data.boardSectionId) as BoardSectionVO
        for (let i = 0; i < sectionVO.items.length; i++) {
            if (sectionVO.items[i].id == this.valueManager.curItemId) {
                return i
            }
        }
        return -1
    }
}
