import { Asset, assetManager } from 'cc'
import { IRender, RenderManager } from '../../core/RenderManager'
import { AssetCache } from '../cache/AssetCache'
import AtlasCache from '../cache/AtlasCache'
import { CacheWarp } from '../cache/BaseCache'
import BufferCache from '../cache/BufferCache'
import JsonCache from '../cache/JsonCache'
import PrefabDataCache from '../cache/PrefabDataCache'
import SkeletonDataCache from '../cache/SkeletonDataCache'
import SpriteFrameCache from '../cache/SpriteFrameCache'
import VideoCache from '../cache/VideoCache'
import LoadEvent from '../events/LoadEvent'
import TaskEvent from '../events/TaskEvent'
import AtlasLoader from './AtlasLoader'
import BinaryLoader from './BinaryLoader'
import { eFileType, eLoaderStatus, eLoaderType, ePriority } from './EnumLoader'
import { ILoader } from './ILoader'
import ImageLoader from './ImageLoader'
import JsonLoader from './JsonLoader'
import LoaderVO from './LoaderVO'
import LoadTask from './LoadTask'
import PrefabLoader from './PrefabLoader'
import SkeletonLoader from './SkeletonLoader'
import VideoLoader from './VideoLoader'

export class AssetLoader implements IRender {
    private _cache: AssetCache = null
    private _taskMap: Map<number, LoadTask> = null
    private _voMap: Map<string, LoaderVO> = null
    private _queueList: Array<string> = null
    private _maxLoader: number = 1
    private _connCounter: number = 0
    private _retryTimes: number = 1
    private _retryDelay: number = 500

    constructor(cache: AssetCache, maxConnection: number = 1, retryTimes: number = 1, retryDelay: number = 500) {
        this._cache = cache
        this._taskMap = new Map()
        this._voMap = new Map()
        this._queueList = []
        this.maxConnection = maxConnection
        this._retryTimes = retryTimes
        this._retryDelay = retryDelay

        RenderManager.me.addRender(this)
    }

    onRender(dt: number, frame: number, time: number): void {
        this.loadNext()
    }

    /**
     * 
     * @param value
     *
     */
    public set maxConnection(value: number) {
        this._maxLoader = Math.max(1, value)
    }

    /**
     * 
     * @return
     *
     */
    public get maxConnection(): number {
        return this._maxLoader
    }

    /**
     * 
     * @param taskId
     * @returns
     */
    public cancel(taskId: number): void {
        const task = this._taskMap.get(taskId)
        if (!task) {
            return
        }
        task.cancel()
    }

    /**
     * 
     * @param url
     * @param thisObj
     * @param onComplete
     * @param onProgress
     * @param onError
     * @param fileType 
     * @param property 
     * @param immediately 
     * @returns id
     */
    public load(
        url: string,
        thisObj: object = null,
        onComplete: () => void = null,
        onError: (err: Error) => void = null,
        onProgress: (progress: number) => void = null,
        fileType: string = '',
        property: number = ePriority.UI,
        immediately: boolean = false
    ): number {
        const task = LoadTask.Create(thisObj, onComplete, onProgress, onError)
        task.addEventListener(TaskEvent.FINISH, this.onTaskFinish, this)
        const vo = this.insertLoad(url, property)
        if (vo) {
            vo.fileType = fileType
            task.attachToLoaderVO(vo)
        }
        this._taskMap.set(task.id, task)
        task.checkThenComplete() // complete

        if (immediately) {
            this.loadNext()
        }
        return task.id
    }

    public loadGroup(
        urls: string[],
        thisObj: object = null,
        onComplete: () => void = null,
        onError: (err: Error) => void = null,
        onProgress: (progress: number) => void = null,
        property: number = ePriority.UI,
        immediately: boolean = false
    ): number {
        const task = LoadTask.Create(thisObj, onComplete, onProgress, onError)
        task.addEventListener(TaskEvent.FINISH, this.onTaskFinish, this)
        for (let i = 0; i < urls.length; i++) {
            const vo = this.insertLoad(urls[i], property)
            if (vo) {
                task.attachToLoaderVO(vo)
            }
        }
        this._taskMap.set(task.id, task)
        task.checkThenComplete() // complete

        if (immediately) {
            this.loadNext()
        }
        return task.id
    }

    protected loadNext(): void {
        while (this._queueList.length > 0 && this._connCounter < this._maxLoader) {
            let url: string = this._queueList.shift()
            let vo: LoaderVO = this._voMap.get(url)
            if (vo && vo.status == eLoaderStatus.READY) {
                this._doLoad(vo)
                this._connCounter++
            }
        }
    }

    private _doLoad(vo: LoaderVO): void {
        let type: string = vo.fileType ? vo.fileType : vo.getFileType()
        let loader: ILoader
        switch (type) {
            case eFileType.JPG:
            case eFileType.JPEG:
            case eFileType.PNG:
                vo.loadType = eLoaderType.IMAGE
                loader = new ImageLoader()
                break
            case eFileType.PREFAB:
                vo.loadType = eLoaderType.PREFAB
                loader = new PrefabLoader()
                break
            case eFileType.SKELETON:
                vo.loadType = eLoaderType.SKELETON
                loader = new SkeletonLoader()
                break
            case eFileType.ATLAS:
                vo.loadType = eLoaderType.ATLAS
                loader = new AtlasLoader()
                break
            case eFileType.JSON:
                vo.loadType = eLoaderType.JSON
                loader = new JsonLoader()
                break
            case eFileType.BIN:
                vo.loadType = eLoaderType.BUFFER
                loader = new BinaryLoader()
                break
            case eFileType.MP4:
                vo.loadType = eLoaderType.VIDEO
                loader = new VideoLoader()
                break
            // case EnumLoader.FILE_TYPE_ZIP:
            //     vo.loadType = EnumLoaderType.ZIP
            //     loader = new ZipLoader()
            //     break
            // case EnumLoader.FILE_TYPE_PAR:
            //     vo.loadType = EnumLoaderType.PAR
            //     loader = new PF_ParticleLoader()
            //     break
            default:
                vo.loadType = eLoaderType.BUFFER
                loader = new BinaryLoader()
                break
        }
        vo.setProgress(0)
        vo.startLoadTime = RenderManager.me.curTime
        console.log('| [AssetLoader._doLoad]----:load resource:', vo.url, vo.loadType, vo.tryTimes)
        loader.load(vo)
    }

    /** LoaderVO  */
    protected onLoadComplete(evt: LoadEvent): void {
        // 
        this._connCounter--
        console.log('', this._connCounter)

        const vo = evt.target as LoaderVO

        // vo0
        // vo
        if (vo.counter == 0) {
            const hasRes = this._cache.checkRes(vo.url)
            if (!hasRes) {
                // 
                console.log('[AssetLoader] ', vo.url)
                assetManager.releaseAsset(vo.content as Asset)
                // vo
                this.recycleVO()
            }
            return
        }

        // 
        switch (vo.loadType) {
            case eLoaderType.IMAGE:
                let spriteFrameCache = CacheWarp(new SpriteFrameCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, spriteFrameCache)
                break

            case eLoaderType.PREFAB:
                let prefabCache = CacheWarp(new PrefabDataCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, prefabCache)
                break

            case eLoaderType.SKELETON:
                let skeletonCache = CacheWarp(new SkeletonDataCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, skeletonCache)
                break

            case eLoaderType.JSON:
                let jsonCache = CacheWarp(new JsonCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, jsonCache)
                break

            case eLoaderType.ATLAS:
                let atlasCache = CacheWarp(new AtlasCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, atlasCache)
                break

            case eLoaderType.BUFFER:
                let bufferCache = CacheWarp(new BufferCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, bufferCache)
                break

            case eLoaderType.VIDEO:
                let videoCache = CacheWarp(new VideoCache(vo.url, vo.finalLoadURL, vo.content))
                this._cache.setRes(vo.url, videoCache)
                break
        }

        // 
        vo.checkTask()
    }

    /** LoaderVO  */
    protected onLoadFail(evt: LoadEvent): void {
        // 
        this._connCounter--
        console.log('', this._connCounter)

        const vo = evt.target as LoaderVO

        // vo0
        // vo
        if (vo.counter == 0) {
            // vo
            this.recycleVO()
            return
        }

        if (vo.tryTimes++ < this._retryTimes) {
            // 
            RenderManager.me.addCallback(
                this,
                () => {
                    vo.reuse()
                    this._queueList.push(vo.url)
                },
                1,
                this._retryDelay
            )
        } else {
            // 
            vo.checkTask()
        }
    }

    protected onTaskFinish(evt: LoadEvent): void {
        const task = evt.target as LoadTask
        // const voList = task.voList.concat() // listtasklist
        this._taskMap.delete(task.id)
        LoadTask.Recycle(task) // taskvoListvo1

        // vo
        this.recycleVO()
    }

    /** vo */
    private recycleVO() {
        this._voMap.forEach((vo) => {
            if (vo.counter == 0) {
                // vo
                if (vo.status == eLoaderStatus.LOADING) {
                    return
                }
                // 
                const idx = this._queueList.indexOf(vo.url)
                if (idx > -1) {
                    this._queueList.splice(idx, 1)
                }

                this._voMap.delete(vo.url)
                LoaderVO.Recycle(vo)
            }
        }, this)
    }

    private insertLoad(url: string, property: number): LoaderVO {
        // console.log('|| AssetLoader insertLoad:' + url);
        let vo: LoaderVO
        if (this.hasRes(url)) {
            // 
            // 
            this._cache.renewal(url)
        } else {
            vo = this._voMap.get(url)
            if (!vo) {
                vo = LoaderVO.Create(url, property)
                this._voMap.set(url, vo)
                vo.addEventListener(LoadEvent.COMPLETE, this.onLoadComplete, this)
                vo.addEventListener(LoadEvent.FAIL, this.onLoadFail, this)

                // 
                const len: number = this._queueList.length
                if (len) {
                    let index = -1
                    for (let i: number = 0; i < len; ++i) {
                        const item = this._voMap.get(this._queueList[i])
                        if (item && item.property > property) {
                            break
                        }
                        index = i
                    }
                    this._queueList.splice(index + 1, 0, url)
                } else {
                    this._queueList[0] = url
                }
            }
        }

        return vo
    }

    private hasRes(url: string): boolean {
        return this._cache.checkRes(url)
    }
}
