import { assetManager, resources, SpriteFrame, VideoClip } from 'cc'
import BaseCache from './BaseCache'

export default class VideoCache extends BaseCache {
    public get data(): VideoClip {
        return this._data as VideoClip
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof VideoClip) {
            resources.release(this._finalURL, VideoClip)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
