import { instantiate, Node, Prefab, resources } from 'cc'
import BaseCache from './BaseCache'

export default class PrefabDataCache extends BaseCache {
    public get data(): Prefab {
        return this._data as Prefab
    }

    public createNode(): Node {
        if (!this.data) {
            console.error('[PrefabCache][createNode] dataNode')
            return null
        }
        return instantiate(this.data)
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof Prefab) {
            resources.release(this._finalURL, Prefab)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
