import { EventDispatcher } from '../../event/EventDispatcher'
import { MaouEvent } from '../../event/MaouEvent'
import { Utils } from '../../util/Utils'
import LoadEvent from '../events/LoadEvent'
import { eLoaderStatus, ePriority } from './EnumLoader'

/**
 * 
 */
export default class LoaderVO extends EventDispatcher {
    private _status: eLoaderStatus = eLoaderStatus.READY
    private _progress: number = 0

    /** url*/
    public url: string
    public property: number
    public tryTimes: number = 0
    public counter: number = 0
    private _content: object = null
    private _error: Error = null

    /**
     *
     * 
     * @type {EnumFileType}
     */
    public fileType: string = ''

    /**
     *
     * 
     * @type {string}
     */
    public loadType: string
    public startLoadTime: number
    public longTimeTag: number

    /**
     * urlurl
     *
     * @type {string}
     */
    public finalLoadURL: string

    private _path: string

    public reuse(): void {
        this._status = eLoaderStatus.READY
    }

    /** FAILLoadEvent.FAIL */
    public fail(err?: Error): void {
        if (this._status == eLoaderStatus.DISPOSE) {
            return
        }

        this._status = eLoaderStatus.FAIL
        this._error = err

        //  FAIL 
        this.dispatchEvent(MaouEvent.Create(LoadEvent, LoadEvent.FAIL))
    }

    /** LOADINGLoadEvent.LOADING */
    public setProgress(v: number): void {
        if (this._status == eLoaderStatus.DISPOSE) {
            return
        }
        this._status = eLoaderStatus.LOADING
        this._progress = v

        //  PROGRESS 
        this.dispatchEvent(MaouEvent.Create(LoadEvent, LoadEvent.PROGRESS))
    }

    /** COMPLETEDLoadEvent.COMPLETE */
    public complete(content: object): void {
        if (this._status == eLoaderStatus.DISPOSE) {
            return
        }
        this._status = eLoaderStatus.COMPLETED
        this._content = content

        //  COMPLETE 
        const evt = MaouEvent.Create(LoadEvent, LoadEvent.COMPLETE)
        this.dispatchEvent(evt)
    }

    /** LoadEvent.CHECK_TASK LoadTask  */
    public checkTask(): void {
        //  CHECK_TASK 
        const evt = MaouEvent.Create(LoadEvent, LoadEvent.CHECK_TASK)
        this.dispatchEvent(evt)
    }

    protected dispose(): void {
        this._status = eLoaderStatus.DISPOSE
        this.property = this._progress = this.counter = this.tryTimes = this.startLoadTime = this.longTimeTag = 0
        this.fileType = this._error = this._content = this.url = this.loadType = this._path = null
        this.removeAll()
    }

    public get status(): eLoaderStatus {
        return this._status
    }

    public get progress(): number {
        return this._progress
    }

    public get content(): object {
        return this._content
    }

    public get error(): Error {
        return this._error
    }

    public get disposed(): boolean {
        return this._status == eLoaderStatus.DISPOSE
    }

    /**
     *
     * url 
     * @readonly
     * @type {string}
     */
    public get path(): string {
        if (!this._path) {
            this._path = Utils.getPath(this.url)
        }

        return this._path
    }

    public getFileType(): string {
        let ret: string
        if (this.fileType) {
            ret = this.fileType
        } else if (this.url) {
            this.fileType = Utils.getFileExtension(this.url)
            ret = this.fileType
        }

        return ret
    }

    // --------------- LoaderVO  start --------------- //
    private static _loaderVOPool: Array<LoaderVO> = []

    public static Create(url: string, property: number = ePriority.UI): LoaderVO {
        let ret: LoaderVO = this.GetLoaderVO()
        ret.url = url
        ret.property = property
        return ret
    }

    private static GetLoaderVO(): LoaderVO {
        let vo: LoaderVO
        if (this._loaderVOPool.length) {
            vo = this._loaderVOPool.shift()
            vo.reuse()
        } else {
            vo = new LoaderVO()
        }
        return vo
    }

    /** DISPOSE */
    public static Recycle(vo: LoaderVO): void {
        if (this._loaderVOPool.indexOf(vo) == -1) {
            this._loaderVOPool.push(vo)
            vo.dispose()
        }
    }
    // --------------- LoaderVO  end --------------- //
}
