import { BufferAsset, resources } from 'cc'
import { ByteArray } from '../../util/ByteArray'
import BaseCache from './BaseCache'

export default class BufferCache extends BaseCache {
    public get data(): BufferAsset {
        return this._data as BufferAsset
    }

    public getByteArray(): ByteArray {
        return new ByteArray(this.data.buffer())
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof BufferAsset) {
            resources.release(this._finalURL, BufferAsset)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
