import { _decorator, Component, Node, director, Camera, color } from 'cc'
import { GameManager } from './manager/GameManager'
import { SectionManager } from './manager/SectionManager'
import { eFileType, eLoaderStatus, eLoaderType, ePriority } from './maou/asset/loader/EnumLoader'
import { AssetManager } from './maou/asset/AssetManager'
import { MaouCore } from './maou/core/MaouCore'
const { ccclass, property } = _decorator

@ccclass('MainScene')
export class Index extends Component {
    @property(Node)
    canvas: Node = null

    @property(Camera)
    camera: Camera = null

    constructor() {
        super()
    }

    start() {
        MaouCore.Reset()

        GameManager.me.layerManager.init(this.canvas)

        AssetManager.me.load(
            'common/open_button/open_button-0.plist',
            this,
            () => {
                GameManager.me.sectionManger.enter('section_index')
            },
            null,
            null,
            eFileType.ATLAS
        )

        // GameManager.me.sectionManger.enter('section_chicken_video')
        // GameManager.me.sectionManger.enter('section_chicken_slider')
        // GameManager.me.sectionManger.enter('section_board_1')
        // GameManager.me.sectionManger.enter('section_fullvideo')
    }
}
