export class ValueManager {
    public curItemId = ''
    private _valueMap: Map<string, any> = new Map()

    get(key: string): any {
        return this._valueMap.get(key)
    }

    set(key: string, value: any): void {
        this._valueMap.set(key, value)
    }

    setBoardFlag(boardSectionId: string, itemId: string, flag: boolean) {
        console.log(`[ValueManager] setBoardFlag: boardSectionId:${boardSectionId}, itemId:${itemId}, flag:${flag}`)
        let board = this._valueMap.get(boardSectionId)
        if (board == null) {
            board = {}
            this._valueMap.set(boardSectionId, board)
        }
        board[itemId] = flag
    }

    getBoardFlag(boardSectionId: string, itemId: string): boolean {
        let board = this._valueMap.get(boardSectionId)
        if (board == null) {
            board = {}
            this._valueMap.set(boardSectionId, board)
        }
        return board[itemId] == true
    }

    clearBoardFlag(boardSectionId: string) {
        this._valueMap.set(boardSectionId, null)
    }
}
