import { Node, VideoPlayer, _decorator, Animation } from 'cc'
import { GameManager } from '../manager/GameManager'
import { AssetManager } from '../maou/asset/AssetManager'
import VideoCache from '../maou/asset/cache/VideoCache'
// import { VideoSectionVO } from '../data/SectionVO'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('PressSectionComponent')
export class PressSectionComponent extends BaseSectionComponent {
    @property(Node)
    area: Node = null

    @property(Animation)
    anim: Animation = null

    onLoaded() {
        this.area.on(Node.EventType.TOUCH_START, this.onTouchStart, this)
        this.area.on(Node.EventType.TOUCH_END, this.onTouchEnd, this)
        this.anim.on(Animation.EventType.FINISHED, this.onFinish, this)
    }

    dispose() {
        this.area.targetOff(this)
        this.anim.targetOff(this)
    }

    onTouchStart() {
        if (this.anim.getState('press').isPlaying) {
            this.anim.resume()
        } else {
            this.anim.play()
        }
    }

    onTouchEnd() {
        this.anim.pause()
    }

    onFinish() {
        // this.clickNext()
        const self = this
        GameManager.me.showHtmlNextBtn(() => {
            GameManager.me.hideHtmlNextBtn()
            self.clickNext()
        })
    }
}
