import { EventDispatcher } from './EventDispatcher'
import { MaouEvent } from './MaouEvent'

const $singleton = Symbol('singleton')

/**
 * 
 */
export default class EventCenter extends EventDispatcher {
    private static _me: EventCenter = null

    /**
     * EventCenter 
     * @param {Symbol} symbol
     */
    constructor(symbol) {
        if (symbol !== $singleton) {
            throw new Error(' EventCenter  EventCenter ')
        }
        super()
    }

    /**
     *  EventCenter 
     * @returns {EventCenter}
     */
    public static get me() {
        if (this._me == null) {
            this._me = new EventCenter($singleton)
        }
        return this._me
    }

    public static Register(type, listener, thisObj = null, priority = 0) {
        this.me.addEventListener(type, listener, thisObj, priority)
    }

    public static Unregister(type, listener) {
        this.me.removeEventListener(type, listener)
    }

    public static Send(event: MaouEvent, target = null) {
        // target
        if (target !== null && target !== undefined) {
            event.target = target
        }

        this.me.dispatchEvent(event)
    }
}
