import { EventTouch, gfx, Node, Sprite, UITransform, VideoPlayer, _decorator } from 'cc'
import { MovieClip } from '../common/MovieClip'
import { GameManager } from '../manager/GameManager'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('SliderSectionComponent')
export class SliderSectionComponent extends BaseSectionComponent {
    @property(Node)
    leftNode: Node = null

    @property(Node)
    rightNode: Node = null

    @property(Node)
    block: Node = null

    @property
    value: number = 0.5

    @property(MovieClip)
    dragBtn: MovieClip = null

    private _leftW = 0
    private _rightW = 0

    onLoaded() {
        this.block.on(Node.EventType.TOUCH_START, this.onBtnTouchStart, this)
        this._leftW = this.leftNode.getComponent(UITransform).width
        this._rightW = this.rightNode.getComponent(UITransform).width
        this.setValue(this.value)
    }

    run() {
        const self = this
        GameManager.me.showHtmlNextBtn(() => {
            GameManager.me.hideHtmlNextBtn()
            self.clickNext()
        })
    }

    update() {
        if (this.dragBtn.curFrame == 34) {
            this.dragBtn.gotoAndPlay(11)
        }
    }

    private onBtnTouchStart(evt: EventTouch) {
        const node = evt.currentTarget as Node
        node.on(Node.EventType.TOUCH_MOVE, this.onTouchMove, this)
        node.on(Node.EventType.TOUCH_END, this.onTouchEnd, this)
        node.on(Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this)
    }

    private onTouchEnd(evt: EventTouch) {
        this.node.targetOff(this)
    }

    private onTouchMove(evt: EventTouch) {
        let pos = this.block.position.add3f(evt.getUIDelta().x, 0, 0)
        if (pos.x < this.leftNode.position.x) {
            pos.x = this.leftNode.position.x
        } else if (pos.x > this.rightNode.position.x) {
            pos.x = this.rightNode.position.x
        }
        this.block.position = pos
        let v = (this.block.position.x - this.leftNode.position.x) / this._leftW
        this.setValue(v)
    }

    private setValue(value: number) {
        this.value = value
        this.leftNode.getComponent(UITransform).width = this._leftW * value
        this.rightNode.getComponent(UITransform).width = this._rightW * (1 - value)
    }
}
