import { _decorator, Component, Node, resources, ImageAsset } from 'cc'
import { BaseSectionVO } from '../data/SectionVO'
import { GameManager } from '../manager/GameManager'
import { SectionManager } from '../manager/SectionManager'
import { ValueManager } from '../manager/ValueManager'
import { AssetManager } from '../maou/asset/AssetManager'
import { EventDispatcher, IEventDispatcher, IListener } from '../maou/event/EventDispatcher'
import { MaouEvent } from '../maou/event/MaouEvent'
const { ccclass, property } = _decorator

@ccclass('BaseSectionComponent')
export class BaseSectionComponent extends Component implements IEventDispatcher {
    public static EventType = {
        Loaded: 'loaded',
        Next: 'next'
    }

    protected _vo: BaseSectionVO = null

    protected _evtDispatcher: EventDispatcher = null

    public isBackRun = false

    constructor() {
        super()
        this._evtDispatcher = new EventDispatcher(this)
    }

    setData(vo: BaseSectionVO) {
        this._vo = vo
    }

    load() {
        if (this._vo == null || this._vo.resource == null || this._vo.resource.length == 0) {
            this.dispatchEvent(new MaouEvent(BaseSectionComponent.EventType.Loaded))
            return
        }

        AssetManager.me.loadGroup(this._vo.resource, this, () => {
            this.dispatchEvent(new MaouEvent(BaseSectionComponent.EventType.Loaded))
        })
    }

    onLoaded() {}

    show(container: Node) {
        this.node.parent = container
    }

    run(...args) {}

    onBack(): object {
        return null
    }

    dispose() {}

    clickNext(evt = null, data = null) {
        this.dispatchEvent(new MaouEvent(BaseSectionComponent.EventType.Next, data))
    }

    get id(): string {
        return this._vo.id
    }

    get data(): BaseSectionVO {
        return this._vo
    }

    get valueManager(): ValueManager {
        return GameManager.me.valueManager
    }

    get sectionManager(): SectionManager {
        return GameManager.me.sectionManger
    }

    // IEventDispatcher 
    dispatchEvent(event: MaouEvent): void {
        this._evtDispatcher.dispatchEvent(event)
    }
    dispatchEventWith(type: string, data?: object): void {
        this._evtDispatcher.dispatchEventWith(type, data)
    }
    addEventListener(type: string, listener: IListener, thisObj?: object, priority?: number): void {
        this._evtDispatcher.addEventListener(type, listener, thisObj, priority)
    }
    once(type: string, listener: IListener, thisObj?: object, priority?: number): void {
        this._evtDispatcher.once(type, listener, thisObj, priority)
    }
    removeEventListener(type: string, listener: IListener) {
        this._evtDispatcher.removeEventListener(type, listener)
    }
    removeAll() {
        this._evtDispatcher.removeAll()
    }
    hasEventListener(type: any): boolean {
        return this._evtDispatcher.hasEventListener(type)
    }
    removeTargetListener(target: object): void {
        this._evtDispatcher.removeTargetListener(target)
    }
}
