import { _decorator, Component, Node, SpriteAtlas, Sprite, Vec3, SpriteFrame, v3 } from 'cc'
const { ccclass, property } = _decorator

@ccclass('MovieClip')
export class MovieClip extends Component {
    @property()
    frameRate = 30

    private _sprite: Sprite = null
    private _pos: Vec3 = null
    private _pos0: Vec3 = null
    private _frames: SpriteFrame[] = []
    private _curFrame = 0
    private _frameLen = 0
    private _timeTotal = 0
    private _frameGap = 0

    start() {
        this._sprite = this.getComponent(Sprite)
        this._frames = this._sprite.spriteAtlas.getSpriteFrames()
        this._frameLen = this._frames.length
        this._pos = this._sprite.node.getPosition()
        if (this._pos0 == null) {
            this._pos0 = v3(this._frames[0].offset.x, this._frames[0].offset.y, 0)
        }
        this._frameGap = 1 / this.frameRate
    }

    update(deltaTime: number) {
        const frame = Math.floor(this._timeTotal / this._frameGap)
        this._curFrame = frame % this._frameLen
        let sp = this._frames[this._curFrame]
        this._sprite.spriteFrame = sp
        this._sprite.node.position = v3(this._pos.x + sp.offset.x - this._pos0.x, this._pos.y + sp.offset.y - this._pos0.y)
        this._timeTotal += deltaTime
    }

    public get curFrame(): number {
        return this._curFrame
    }

    public gotoAndPlay(frame: number) {
        this._timeTotal = this._frameGap * frame
    }
}
