import { JsonAsset, resources } from 'cc'
import BaseCache from './BaseCache'

export default class JsonCache extends BaseCache {
    public get data(): JsonAsset {
        return this._data as JsonAsset
    }

    public get json(): object {
        return this.data ? this.data.json : null
    }

    protected addRef() {
        this.data && this.data.addRef()
    }

    protected decRef() {
        this.data && this.data.decRef()
    }

    public get refCount(): number {
        return this.data == null ? 0 : this.data.refCount
    }

    /**
     * 
     */
    public destroy(): void {
        if (this._data != null && this._data instanceof JsonAsset) {
            resources.release(this._finalURL, JsonAsset)
            // assetManager.releaseAsset(this._data)
            this._data.destroy()
            this._data = null
        }
        this._isDisposed = true
    }
}
