export interface IRender {
    /**
     * 
     * @param {number} dt s
     * @param {number} frame 
     * @param {number} time ms
     */
    onRender: (dt: number, frame: number, time: number) => void
}

export class RenderManager {
    private static _me: RenderManager = null

    private _curTime: number = 0
    private _preFrameTime: number
    private _lastTime: number = 0
    private _frameCount: number = 0
    private _renderList: IRender[] = null
    private _callbackList: IFrameCallback[] = null

    public static get me(): RenderManager {
        if (this._me == null) {
            this._me = new RenderManager()
        }
        return this._me
    }

    // constructor() {
    //     this.reset()
    // }

    public reset() {
        this._curTime = Date.now()
        this._lastTime = this._curTime
        this._frameCount = 0
        this._renderList = []
        this._callbackList = []
    }

    public get preFrameTime(): number {
        return this._preFrameTime
    }

    public get curTime(): number {
        return this._curTime
    }

    public destroy() {}

    public update() {
        this._preFrameTime = this._curTime
        this._frameCount++
        this._curTime = Date.now()
        const dt = (this._curTime - this._lastTime) / 1000 // 

        this.rendeRender(dt, this._frameCount, this._curTime)
        this.rendeCallback(this._curTime)

        this._lastTime = this._curTime
    }

    /**
     * 
     * @param value 
     * @param num (0)
     * @param delay (0)
     *
     */
    public addCallback(caller: any, value: () => void, num: number = 0, delay: number = 0): void {
        if (value != null && !this.hasCallback(value)) {
            let vo: IFrameCallback = {
                caller: caller,
                callback: value,
                num: num,
                delay: delay,
                lastTime: this._curTime,
                curNum: 0
            }
            this._callbackList.push(vo)
        }
    }

    public hasCallback(value: Function): boolean {
        return this._callbackList.some((item) => {
            return item.callback == value
        })
    }

    public removeCallback(value: Function): void {
        for (let i = 0; i < this._callbackList.length; i++) {
            if (this._callbackList[i].callback == value) {
                this._callbackList.splice(i, 1)
                return
            }
        }
    }

    public removeCallbackTarget(target: any): void {
        for (let i = this._callbackList.length - 1; i >= 0; i--) {
            if (this._callbackList[i].caller == target) {
                this._callbackList.splice(i, 1)
            }
        }
    }

    public addRender(value: IRender): void {
        if (value != null && !this.hasRender(value)) {
            this._renderList.push(value)
        }
    }

    public hasRender(value: IRender): boolean {
        return this._renderList.indexOf(value) >= 0
    }

    public removeRender(value: IRender): void {
        if (value != null) {
            const idx = this._renderList.indexOf(value)
            if (idx >= 0) {
                this._renderList.splice(idx, 1)
            }
        }
    }

    private rendeRender(dt: number, frame: number, time: number) {
        this._renderList.forEach((render) => {
            render.onRender(dt, frame, time)
        }, this)
    }

    private rendeCallback(curTime: number): void {
        let vo: IFrameCallback
        let addTime: number
        for (vo of this._callbackList) {
            addTime = curTime - vo.lastTime

            if (addTime >= vo.delay) {
                if (vo.delay == 0) {
                    vo.lastTime = curTime
                } else {
                    vo.lastTime = curTime + (addTime % vo.delay)
                }

                vo.callback.call(vo.caller)
                if (vo.num != 0) {
                    vo.curNum += parseInt((addTime / vo.delay).toString())
                    if (vo.curNum >= vo.num) {
                        let index: number = this._callbackList.indexOf(vo)
                        if (index > -1) {
                            this._callbackList.splice(index, 1)
                        }
                    }
                }
            }
        }
    }
}

interface IFrameCallback {
    delay: number
    num: number
    curNum: number
    callback: () => void
    lastTime: number
    caller: any
}
