import { RenderManager } from '../../core/RenderManager'
import { LoaderConfig } from '../loader/EnumLoader'
import AtlasCache from './AtlasCache'
import BaseCache from './BaseCache'
import BufferCache from './BufferCache'
import JsonCache from './JsonCache'
import PrefabDataCache from './PrefabDataCache'
import SkeletonDataCache from './SkeletonDataCache'
import SpriteFrameCache from './SpriteFrameCache'
import VideoCache from './VideoCache'

export class AssetCache {
    /**  */
    private _activeMap: Map<string, BaseCache> = null
    /** 0 */
    private _inertiaMap: Map<string, BaseCache> = null

    constructor() {
        this._activeMap = new Map()
        this._inertiaMap = new Map()
        RenderManager.me.addCallback(this, this._renderDispose, 0, 5000) // 5
    }

    public destroy() {
        if (this._activeMap) {
            this._activeMap.forEach((cache) => {
                cache.destroy()
            }, this)
            this._activeMap.clear()
            this._activeMap = null
        }

        if (this._inertiaMap) {
            this._inertiaMap.forEach((cache) => {
                cache.destroy()
            }, this)
            this._inertiaMap.clear()
            this._inertiaMap = null
        }
        RenderManager.me.removeCallback(this._renderDispose)
    }

    public setRes(url: string, obj: BaseCache): void {
        this._activeMap.set(url, obj)
    }

    /**
     * 
     * +1
     *  checkRes
     *
     * @param {string} url
     * @returns {BaseCache}
     */
    public getRes(url: string): BaseCache {
        let cache = this._activeMap.get(url)
        if (cache == null && this._inertiaMap.has(url)) {
            cache = this._inertiaMap.get(url)
            this.setActive(cache)
        }
        cache.getOnce()
        return cache
    }

    /** */
    public setActive(cache: BaseCache): void {
        if (this._inertiaMap != null && cache != null) {
            this._inertiaMap.delete(cache.url)
            this._activeMap.set(cache.url, cache)
        }
    }

    /** */
    public setInertia(cache: BaseCache): void {
        if (this._activeMap != null && cache != null) {
            this._activeMap.delete(cache.url)
            this._inertiaMap.set(cache.url, cache)

            this.renewal(cache.url)
        }
    }

    /**  */
    public renewal(url: string): void {
        let cache = this._inertiaMap.get(url)
        if (cache) {
            cache.lifeTime = RenderManager.me.curTime
        }
    }

    public checkRes(url: string): boolean {
        return this._activeMap.has(url) || this._inertiaMap.has(url)
    }

    /** SpriteFrame*/
    public getSpriteFrame(url: string): SpriteFrameCache {
        return this.getRes(url) as SpriteFrameCache
    }

    /** */
    public getPrefab(url: string): PrefabDataCache {
        return this.getRes(url) as PrefabDataCache
    }

    /**  */
    public getSkeleton(url: string): SkeletonDataCache {
        return this.getRes(url) as SkeletonDataCache
    }

    /** json */
    public getJson(url: string): JsonCache {
        return this.getRes(url) as JsonCache
    }

    /**  */
    public getAtlas(url: string): AtlasCache {
        return this.getRes(url) as AtlasCache
    }

    /**  */
    public getBuffer(url: string): BufferCache {
        return this.getRes(url) as BufferCache
    }

    public getVideo(url: string): VideoCache {
        return this.getRes(url) as VideoCache
    }

    /**  */
    public disposeAll() {
        this.disposeCache(true, 0)
    }

    /**
     *
     * @param clearAll 
     * @param curTime
     * @param clearTime clearTime = 60000 curTime 
     */
    public disposeCache(clearAll: boolean, curTime: number, clearTime: number = 60000): void {
        let keys = Array.from(this._inertiaMap.keys())
        for (let i = 0; i < keys.length; i++) {
            const url = keys[i]
            const cache = this._inertiaMap.get(url)
            if (cache) {
                if (clearAll || curTime - cache.lifeTime >= clearTime) {
                    this.disposeFromCache(url)
                }
            }
        }
    }

    /**
     * 
     * @param url
     *
     */
    public disposeFromCache(url: string): boolean {
        let ret: boolean
        let vo = this._activeMap.get(url)
        if (vo == null) {
            vo = this._inertiaMap.get(url)
        }
        if (vo) {
            ret = true
            vo.destroy()
            console.log('| -------disposeFromCache:', url)
            this._activeMap.delete(url)
            this._inertiaMap.delete(url)
        }
        return ret
    }

    private _renderDispose(): void {
        let curTime: number = RenderManager.me.curTime
        this.disposeCache(false, curTime, LoaderConfig.MAX_LIFE_TIME)
    }
}
