import { color, EventTouch, Node, size, Sprite, tween, UITransform, v2, v3, Vec3, _decorator } from 'cc'
import { BoardSectionVO } from '../data/SectionVO'
import { AssetManager } from '../maou/asset/AssetManager'
import BaseCache from '../maou/asset/cache/BaseCache'
import SpriteFrameCache from '../maou/asset/cache/SpriteFrameCache'
import { RenderManager } from '../maou/core/RenderManager'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('BoardSectionComponent')
export class BoardSectionComponent extends BaseSectionComponent {
    @property(Node)
    btn1: Node = null

    @property(Node)
    btn2: Node = null

    @property(Node)
    btn3: Node = null

    @property(Node)
    fridgeBase: Node = null

    @property(Node)
    fridgeTray: Node = null

    @property(Node)
    hotPoint: Node = null

    @property(Node)
    dragNode: Node = null

    @property(Sprite)
    dragIcon: Sprite = null

    @property(Sprite)
    bigImage: Sprite = null

    @property(Node)
    dragBg: Node = null

    @property(Sprite)
    dragText: Sprite = null

    private _curBtnIdx: number = 0
    private _curItemId: string = ''
    private _isOpen = false
    private _isDraged = false
    private _cacheMap: Map<string, BaseCache> = new Map()

    get data(): BoardSectionVO {
        return this._vo as BoardSectionVO
    }

    start() {
        this.dragNode.active = false
        this.dragText.node.active = false
    }

    run(openItemId: string = null) {
        console.log(`[BoardSection] run opendItemId:${openItemId}, isBackRun:${this.isBackRun}`)

        // item
        if (this.isBackRun) {
            this.valueManager.setBoardFlag(this.id, openItemId, false)
        }

        // item
        this.data.items.forEach((itemvo) => {
            this._getBigImage(itemvo.bigImage, null)
        })

        if (openItemId != null) {
            if (!this.isBackRun) {
                // 
                this._isDraged = true
                this.fridgeBase.position = v3(0, 0)
                this.fridgeTray.position = v3(0, 36)
                this.hotPoint.active = false
                this._isOpen = true

                this._curItemId = openItemId
                this.valueManager.setBoardFlag(this.id, this._curItemId, true)
                this._isDraged = true

                // 
                const itemvo = this._getCurItem()
                this._getBigImage(itemvo.bigImage, (cache: SpriteFrameCache) => {
                    this.bigImage.spriteFrame = cache.data
                    this.dragNode.active = false
                    this._endSection()
                })
            }
        }

        this._updateButon()
        this._registerEvent()
    }

    dispose() {
        this._unRegisterEvent()
        // this._cacheMap.forEach((item) => {
        //     item.dispose()
        // })
        this._cacheMap.clear()
        this._cacheMap = null
    }

    // 
    clickOpenTray() {
        tween(this.fridgeBase)
            .to(0.5, { position: v3(0, 0) }, { easing: 'quadOut' })
            .start()

        tween(this.fridgeTray)
            .to(0.5, { position: v3(0, 36) }, { easing: 'quadOut' })
            .start()

        this.dragText.node.active = true

        this.hotPoint.active = false
        this._isOpen = true
    }

    // 
    private _updateButon() {
        this.data.items.forEach((item, index) => {
            const flag = this.valueManager.getBoardFlag(this.id, item.id)
            const disable = flag || this._curBtnIdx == index + 1
            const btnNode = this[`btn${index + 1}`] as Node
            btnNode.getChildByName('icon1').active = !disable
            btnNode.getChildByName('icon2').active = disable
        }, this)
    }

    private _registerEvent() {
        this.data.items.forEach((item, index) => {
            const btnNode = this[`btn${index + 1}`] as Node
            btnNode.on(Node.EventType.TOUCH_START, this.onBtnTouchStart, this)
        }, this)
    }

    private _unRegisterEvent() {
        this.data.items.forEach((item, index) => {
            const btnNode = this[`btn${index + 1}`] as Node
            btnNode.targetOff(this)
        }, this)
    }

    private onBtnTouchStart(evt: EventTouch) {
        if (!this._isOpen || this._isDraged) {
            return
        }
        const node = evt.currentTarget as Node
        if (node.getChildByName('icon1').active == false) {
            return
        }

        this.dragText.node.active = false
        this._curBtnIdx = Number(node.name.split('Btn')[1])
        const itemVO = this.data.items[this._curBtnIdx - 1]
        this._curItemId = itemVO.id
        this._updateButon()
        this.node.on(Node.EventType.TOUCH_MOVE, this.onTouchMove, this)
        this.node.on(Node.EventType.TOUCH_END, this.onTouchEnd, this)
        this.node.on(Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this)
        let posV3 = v3()
        let localUI = evt.getUILocation()
        let uiSpaceWorldPos = v3(localUI.x, localUI.y, 0)
        const uiTrans = this.node.getComponent(UITransform)
        uiTrans.convertToNodeSpaceAR(uiSpaceWorldPos, posV3)
        this.dragNode.position = posV3
        this.dragNode.active = true
        this.dragIcon.spriteFrame = node.getChildByName('icon1').getComponent(Sprite).spriteFrame

        if (itemVO.dragConf) {
            const uiTran = this.dragIcon.node.getComponent(UITransform)
            uiTran.setContentSize(size(itemVO.dragConf.size.width, itemVO.dragConf.size.height))
            this.dragBg.active = itemVO.dragConf.showBg
        }
    }

    private onTouchEnd(evt: EventTouch) {
        const node = evt.currentTarget as Node
        this.node.targetOff(this)
        if (this.dragNode.position.x >= -470 && this.dragNode.position.x <= 500 && this.dragNode.position.y >= -160 && this.dragNode.position.y <= 260) {
            // 
            tween(this.dragNode)
                .to(0.5, { position: v3(0, 20) }, { easing: 'quadOut' })
                .call(() => {
                    this.valueManager.setBoardFlag(this.id, this._curItemId, true)
                    this._isDraged = true

                    // 
                    const itemvo = this._getCurItem()
                    this._getBigImage(itemvo.bigImage, (cache: SpriteFrameCache) => {
                        this.bigImage.spriteFrame = cache.data
                        this.dragNode.active = false

                        // 2section
                        // RenderManager.me.addCallback(
                        //     this,
                        //     () => {
                        //         this.valueManager.curItemId = this._curItemId
                        //         this.clickNext(null, this._curItemId)
                        //     },
                        //     1,
                        //     2000
                        // )
                        this._endSection()
                    })
                })
                .start()
        } else {
            // 
            this.dragText.node.active = true
            const btn = this[`btn${this._curBtnIdx}`] as Node
            let pos = btn.position
            let uiTrans = btn.parent.getComponent(UITransform)
            pos = uiTrans.convertToWorldSpaceAR(pos)
            pos = this.node.getComponent(UITransform).convertToNodeSpaceAR(pos)

            tween(this.dragNode)
                .to(0.5, { position: pos }, { easing: 'quadOut' })
                .call(() => {
                    this.dragNode.active = false
                    this._curBtnIdx = 0
                    this._updateButon()
                })
                .start()
        }
    }

    private _endSection(waitTime = 2000) {
        // 2section
        RenderManager.me.addCallback(
            this,
            () => {
                this.valueManager.curItemId = this._curItemId
                this.clickNext(null, this._curItemId)
            },
            1,
            waitTime
        )
    }

    private onTouchMove(evt: EventTouch) {
        this.dragNode.position = this.dragNode.position.add3f(evt.getUIDelta().x, evt.getUIDelta().y, 0)
    }

    private _getBigImage(path: string, cb: Function) {
        if (this._cacheMap.has(path)) {
            const cache = this._cacheMap.get(path)
            cb && cb.call(this, cache)
        } else {
            AssetManager.me.load(path, this, () => {
                const cache = AssetManager.me.cache.getRes(path)
                this._cacheMap.set(path, cache)
                cb && cb.call(this, cache)
            })
        }
    }

    private _getCurItem() {
        for (let i = 0; i < this.data.items.length; i++) {
            if (this.data.items[i].id == this._curItemId) {
                return this.data.items[i]
            }
        }
        return null
    }

    clickSwitch(evt, data: string) {
        const arg: { hide: string[]; show: string[] } = JSON.parse(data)
        arg.hide.forEach((nodeName) => {
            this.node.getChildByPath(nodeName).active = false
        })
        arg.show.forEach((nodeName) => {
            this.node.getChildByPath(nodeName).active = true
        })
    }
}
