import { AssetCache } from './cache/AssetCache'
import { AssetLoader } from './loader/AssetLoader'
import { ePriority, LoaderConfig } from './loader/EnumLoader'

export class AssetManager {
    private static _me: AssetManager = null

    private _cache: AssetCache = null
    private _loader: AssetLoader = null

    constructor() {
        this._cache = new AssetCache()
        this._loader = new AssetLoader(this._cache, LoaderConfig.GAMING_CONNECTION, LoaderConfig.LOAD_FAILED_TRY_TIMES)
    }

    public static get me(): AssetManager {
        if (this._me == null) {
            this._me = new AssetManager()
        }
        return this._me
    }

    /**
     * 
     *
     * @param url
     * @param 
     * @param 
     * @param 
     * @param urlfileType
     * @param 
     * @param 
     * @returns TaskId
     */
    public load(
        url: string,
        thisObj: object,
        onComplete?: () => void,
        onError?: (err: Error) => void,
        onProgress?: (progress: number) => void,
        fileType: string = '',
        property: number = ePriority.UI,
        immediately: boolean = false
    ): number {
        return this._loader.load(url, thisObj, onComplete, onError, onProgress, fileType, property, immediately)
    }

    public loadGroup(
        urls: string[],
        thisObj: object = null,
        onComplete: () => void = null,
        onError: (err: Error) => void = null,
        onProgress: (progress: number) => void = null,
        property: number = ePriority.UI,
        immediately: boolean = false
    ): number {
        return this._loader.loadGroup(urls, thisObj, onComplete, onError, onProgress, property, immediately)
    }

    public cancel(taskId: number): void {
        this._loader.cancel(taskId)
    }

    public get cache(): AssetCache {
        return this._cache
    }
}
