import { MaouEvent } from './MaouEvent'

export interface IListener {
    (event: MaouEvent): void
}

class Listener {
    fn: IListener = null
    thisObj: object = null
    priority = 0
    once = false

    constructor(fn: IListener, thisObj: object, priority = 0, once = false) {
        this.fn = fn
        this.thisObj = thisObj
        this.priority = priority
        this.once = once
    }
}

export interface IEventDispatcher {
    dispatchEvent(event: MaouEvent): void
    dispatchEventWith(type: string, data?: object): void
    addEventListener(type: string, listener: IListener, thisObj?: object, priority?: number): void
    once(type: string, listener: IListener, thisObj?: object, priority?: number): void
    removeEventListener(type: string, listener: IListener)
    removeAll()
    hasEventListener(type): boolean
    removeTargetListener(target: object): void
}

export class EventDispatcher implements IEventDispatcher {
    private _target: object = null
    private _listenerMap: Map<string, Listener[]> = null

    /**
     *  EventDispatcher 
     *
     * @param target  EventDispatcher  target 
     *  IEventDispatcher 
     *  target 
     *  EventDispatcher 
     */
    constructor(target = null) {
        this._listenerMap = new Map()
        this._target = target
    }

    /**
     * 
     * @param {MaouEvent} event  Event 
     */
    public dispatchEvent(event: MaouEvent): void {
        if (!this.hasEventListener(event.type)) {
            return
        }

        // target
        if (event.target === null) {
            if (this._target !== null) {
                event.target = this._target
            } else {
                event.target = this
            }
        }

        const listenerList = this._listenerMap.get(event.type).concat()
        listenerList.forEach((listener) => {
            const { fn, thisObj } = listener

            if (listener.once) {
                this.removeEventListener(event.type, fn)
            }

            if (thisObj) {
                fn.call(thisObj, event)
            } else {
                fn.call(this, event)
            }
        }, this)

        // Event
        MaouEvent.Recycle(event)
    }

    public dispatchEventWith(type: string, data = null): void {
        this.dispatchEvent(MaouEvent.Create(MaouEvent, type, data))
    }

    /**
     *  EventDispatcher 
     * @param {string} type 
     * @param {(event: MaouEvent) => void} listener  Event  function(evt:Event):void 
     * @param {{}} thisObj this
     * @param {number} priority  n  n -1  0
     */
    public addEventListener(type: string, listener: IListener, thisObj: object = null, priority = 0): void {
        this._addEventListener(type, listener, thisObj, priority)
    }

    /**
     * addEventListener()addEventListener()
     * @param {string|Symbol} type 
     * @param {function} listener  Event  function(evt:Event):void 
     * @param {{}} thisObj this
     * @param {number} priority  n  n -1  0
     */
    public once(type: string, listener: IListener, thisObj: object = null, priority = 0): void {
        this._addEventListener(type, listener, thisObj, priority, true)
    }

    private _addEventListener(type: string, listener: IListener, thisObj = null, priority = 0, once = false): void {
        if (!this.hasEventListener(type)) {
            this._listenerMap.set(type, [])
        }

        const listeners = this._listenerMap.get(type)

        for (let i = 0; i < listeners.length; i++) {
            if (listeners[i].fn === listener) {
                //
                //
                if (listeners[i].priority !== priority) {
                    listeners[i].priority = priority
                    listeners.sort((a, b) => {
                        return b.priority - a.priority
                    })
                }
                return
            }
        }

        listeners.push(new Listener(listener, thisObj, priority, once))
        listeners.sort((a, b) => {
            return b.priority - a.priority
        })
    }

    /**
     *  EventDispatcher 
     * @param {string|Symbol} type 
     * @param {function} listener 
     */
    public removeEventListener(type: string, listener: IListener): void {
        if (!this.hasEventListener(type)) {
            return
        }

        const listeners = this._listenerMap.get(type)
        for (let i = listeners.length - 1; i >= 0; i--) {
            if (listeners[i].fn === listener) {
                listeners.splice(i, 1)
                break
            }
        }

        if (listeners.length === 0) {
            this._listenerMap.delete(type)
        }
    }

    public removeTargetListener(target: object): void {
        const keys = this._listenerMap.keys()
        const evtTypes = Array.from(keys)
        for (let i = 0; i < evtTypes.length; i++) {
            const evtType = evtTypes[i]
            const listeners = this._listenerMap.get(evtType)
            for (let j = listeners.length - 1; j >= 0; j--) {
                if (listeners[j].thisObj === target) {
                    this.removeEventListener(evtType, listeners[j].fn)
                }
            }
        }
    }

    /**
     *  EventDispatcher 
     */
    public removeAll(): void {
        this._listenerMap.clear()
    }

    /**
     *  EventDispatcher 
     */
    public hasEventListener(type): boolean {
        return this._listenerMap.has(type)
    }
}
