    enter(sectionId: string, ...args) {
        return new Promise<boolean>((resolve) => {
            if (this._entering) {
                return resolve(false)
            }
            this._entering = true

            const mainContainer = GameManager.me.layerManager.mainContainer

            const vo = this._sectionMap.get(sectionId)
            const oldSection = this._curSection
            const oldCache = this._curSectionPrefabCache

            this._history.push(sectionId)

            AssetManager.me.load(vo.path, this, () => {
                const cache = AssetManager.me.cache.getRes(vo.path) as PrefabDataCache
                if (!cache || !cache.data) {
                    return resolve(false)
                }
                console.log(cache.data)
                let sectionNode: Node
                try {
                    sectionNode = instantiate(cache.data)
                } catch (err) {
                    console.log(err)
                }
                const newSection = sectionNode.getComponent(BaseSectionComponent)
                newSection.setData(vo)

                // newSection loaded
                newSection.once(
                    BaseSectionComponent.EventType.Loaded,
                    () => {
                        newSection.isBackRun = this._isBack
                        newSection.onLoaded()
                        newSection.show(mainContainer)
                        newSection.run(...args)

                        if (oldSection) {
                            oldSection.dispose()
                            oldSection.node.destroy()
                        }

                        // if (oldCache) {
                        //     oldCache.dispose()
                        // }

                        this._curSection = newSection
                        this._curSectionPrefabCache = cache
                        this._entering = false
                        this._isBack = false
                        return resolve(true)
                    },
                    this
                )

                // next section
                newSection.once(
                    BaseSectionComponent.EventType.Next,
                    (evt) => {
                        this._next(evt.data)
                    },
                    this
                )

                newSection.load()
            })
        })
    }

    private _next(data: any) {
        // if (this._curSection.id == 'section_index') {
        //     this.enter('section_fullvideo')
        // }
        // else if (this._curSection.id == 'section_fullvideo') {
        //     this.enterHome()
        //     GameManager.me.hideHtmlNextBtn()
        // }
        if (this._curSection.id == 'section_index') {
            GameManager.me.showHtmlBg()
            GameManager.me.hideGame()
            GameManager.me.hideHtmlBackBtn()
            let self = this
            GameManager.me.showHtmlNextBtn(() => {
                self.enterHome()
            })
        } else if (this._curSection.id == 'section_home') {
            GameManager.me.hideHtmlBackBtn()
            if (data == 'btn1') {
                this.enterBoard('section_board_1')
            } else if (data == 'btn2') {
                this.enterBoard('section_board_2')
            } else if (data == 'btn3') {
                this.enterBoard('section_board_3')
            }
            GameManager.me.hideHtmlBg()
        } else if (this._curSection.id == 'section_board_1') {
            if (data == 'item_chicken') {
                this.enter('section_chicken_video')
            } else if (data == 'item_cabbage') {
                this.enter('section_cabbage_video')
            } else if (data == 'item_berries') {
                this.enter('section_berries_video')
            }
        } else if (this._curSection.id == 'section_chicken_video') {
            this.enter('section_chicken_slider')
        } else if (this._curSection.id == 'section_cabbage_video') {
            this.enter('section_cabbage_slider')
        } else if (this._curSection.id == 'section_berries_video') {
            this.enter('section_berries_slider')
        } else if (this._curSection.id == 'section_chicken_slider' || this._curSection.id == 'section_cabbage_slider' || this._curSection.id == 'section_berries_slider') {
            this.enter('section_board_1_ending')
        } else if (this._curSection.id == 'section_board_1_ending') {
            if (data == 'home') {
                this.enterHome()
            } else if (data == 'btn1') {
                this.enter('section_board_1', 'item_chicken')
            } else if (data == 'btn2') {
                this.enter('section_board_1', 'item_cabbage')
            } else if (data == 'btn3') {
                this.enter('section_board_1', 'item_berries')
            }
        }

        if (this._curSection.id == 'section_board_2') {
            if (data == 'item_juice') {
                this.enter('section_juice_video')
            } else if (data == 'item_drumstick') {
                this.enter('section_drumstick_press')
            }
        } else if (this._curSection.id == 'section_juice_video') {
            this.enter('section_juice_image')
            const self = this
            GameManager.me.showHtmlNextBtn(() => {
                self._next(null)
                GameManager.me.hideHtmlNextBtn()
            })
        } else if (this._curSection.id == 'section_juice_image' || this._curSection.id == 'section_drumstick_press') {
            this.enter('section_board_2_ending')
        } else if (this._curSection.id == 'section_board_2_ending') {
            if (data == 'home') {
                this.enterHome()
            } else if (data == 'btn1') {
                this.enter('section_board_2')
            } else if (data == 'btn2') {
                this.enter('section_board_2')
            }
        }

        if (this._curSection.id == 'section_board_3') {
            if (data == 'item_orange') {
                this.enter('section_orange_video')
            } else if (data == 'item_beer') {
                this.enter('section_beer_video')
            } else if (data == 'item_brocolli') {
                this.enter('section_brocolli_video')
            }
        } else if (this._curSection.id == 'section_brocolli_video') {
            this.enter('section_brocolli_slider')
        } else if (this._curSection.id == 'section_orange_video' || this._curSection.id == 'section_beer_video' || this._curSection.id == 'section_brocolli_slider') {
            this.enter('section_board_3_ending')
        } else if (this._curSection.id == 'section_board_3_ending') {
            if (data == 'home') {
                this.enterHome()
            } else {
                this.enter('section_board_3')
            }
        }
    }

    private enterHome() {
        GameManager.me.valueManager.clearBoardFlag('section_board_1')
        GameManager.me.valueManager.clearBoardFlag('section_board_2')
        GameManager.me.valueManager.clearBoardFlag('section_board_3')
        this._history.length = 1
        this.enter('section_home').then((v) => {
            GameManager.me.showGame()
            GameManager.me.showHtmlBg()
            GameManager.me.hideHtmlNextBtn()
            GameManager.me.hideHtmlBackBtn()
        })
    }

    private enterBoard(sectionId: string) {
        this.enter(sectionId)
        const self = this
        GameManager.me.showHtmlBackBtn(() => {
            if (self._history.length >= 3) {
                if (self._history.length == 3) {
                    GameManager.me.hideHtmlBackBtn()
                }

                self._history.pop()
                const id = self._history.pop()
                const itemId = self._curSection.data.itemId
                this._isBack = true
                self.enter(id, itemId)
            }
        })
    }

    getSection(id: string) {
        return this._sectionMap.get(id)
    }
