export class Utils {
    /** @private */
    protected static _extReg: RegExp = /* [STATIC SAFE]*/ /\.(\w+)\??/g

    public static getPath(path: string): string {
        let result: Array<string> = path.split('.')
        if (result) {
            return result[0]
        }
        return null
    }

    public static getFileExtension(path: string): string {
        this._extReg.lastIndex = path.lastIndexOf('.')
        let result: Array<any> = this._extReg.exec(path)
        if (result && result.length > 1) {
            return result[1].toLowerCase()
        }
        return null
    }
}
