import { _decorator, Component, Node, game } from 'cc'
import MaouCommand, { CommandState } from './MaouCommand'
import MaouController from './MaouController'
import { RenderManager } from './RenderManager'
const { ccclass, property } = _decorator

@ccclass('MaouCore')
export class MaouCore extends Component {
    static Instance: MaouCore
    private static Controllers: Map<any, MaouController> = new Map<any, MaouController>()

    public commands: Array<MaouCommand>
    public command: MaouCommand

    onLoad() {
        MaouCore.Instance = this
        this.commands = new Array<MaouCommand>()
    }

    update(dt) {
        if (this.command != null) {
            this.checkCommand()
        }

        RenderManager.me.update()
    }

    onDestroy() {
        MaouCore.Instance = null
        // MaouCore.Controllers.clear()
        // MaouCore.Caches.clear()

        // this.command = null
        // this.commands.length = 0
    }

    static Reset() {
        if (MaouCore.Instance != null) {
            MaouCore.Instance.node.destroy()
        }
        let node = new Node('[MaouCore]')
        node.addComponent(MaouCore)
        // 
        game.addPersistRootNode(node)
        RenderManager.me.reset()
    }

    private checkCommand() {
        if (this.command != null && this.command.state === CommandState.Ready) {
            this.command.state = CommandState.Finish
            this.directCall(this.command)
        }

        if (this.command != null && this.command.state === CommandState.Finish) {
            this.command = null
            this.nextCommand()
        }
    }
}
