import { Button, Node, _decorator } from 'cc'
import { BoardSectionVO, EndingSectionVO } from '../data/SectionVO'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('EndingSection3Component')
export class EndingSection3Component extends BaseSectionComponent {
    @property(Node)
    btnBack: Node = null

    @property(Node)
    image1: Node = null

    @property(Node)
    image2: Node = null

    @property(Node)
    image3: Node = null

    @property(Node)
    btnNext: Node = null

    get data(): EndingSectionVO {
        return this._vo as EndingSectionVO
    }

    onLoaded() {
        this.btnNext.active = false
        this.btnBack.active = false
        for (let i = 0; i < 3; i++) {
            const image = this[`image${i + 1}`] as Node
            if (image) {
                image.active = i == this.getItemIndex()
            }
        }

        let count = 0
        const boardSectionId = this.data.boardSectionId
        if (this.valueManager.getBoardFlag(boardSectionId, 'item_brocolli')) {
            count += 1
        }
        if (this.valueManager.getBoardFlag(boardSectionId, 'item_orange')) {
            count += 1
        }
        if (this.valueManager.getBoardFlag(boardSectionId, 'item_beer')) {
            count += 1
        }
        if (count >= 2) {
            this.btnBack.active = true
        } else {
            this.btnNext.active = true
        }
    }

    dispose() {}

    clickHome() {
        super.clickNext(null, 'home')
    }

    private getItemIndex() {
        const sectionVO = this.sectionManager.getSection(this.data.boardSectionId) as BoardSectionVO
        for (let i = 0; i < sectionVO.items.length; i++) {
            if (sectionVO.items[i].id == this.valueManager.curItemId) {
                return i
            }
        }
        return -1
    }
}
