import { ImageAsset, resources, VideoPlayer, _decorator } from 'cc'
import { VideoSectionVO } from '../data/SectionVO'
import { GameManager } from '../manager/GameManager'
// import { VideoSectionVO } from '../data/SectionVO'
import { BaseSectionComponent } from './BaseSectionComponent'
const { ccclass, property } = _decorator

@ccclass('FullVideoSectionComponent')
export class FullVideoSectionComponent extends BaseSectionComponent {
    get data(): VideoSectionVO {
        return this._vo as VideoSectionVO
    }

    onLoaded() {
        const videoEle = window.document.createElement('video')
        videoEle.src = 'https://p-games.silversea-media.net/Videos/Nanoex%20Smoke%20Animation%20Empty%20Tray.mp4'
        window.document.body.appendChild(videoEle)
        videoEle.id = 'CocosVideo'
        videoEle.style.position = 'absolute'
        videoEle.style.width = '100%'
        videoEle.style.height = '100%'
        videoEle.loop = true
        videoEle.oncanplay = () => {
            videoEle.play()
        }
        const self = this
        GameManager.me.showHtmlNextBtn(() => {
            self.clickNext()
        })

        this.showLogo()
        this.showText()
    }

    run() {}

    dispose() {
        this.hideHtmlNextBtn()
        this.hideLogo()
        this.hideText()
        this.hideVideo()
    }

    showHtmlNextBtn(clickCallback: () => void) {
        let loadBtnImage = (cb: (src: string) => void) => {
            resources.load('product/PrimeFreeze/image/btn_next_2', (err, data: ImageAsset) => {
                const currentSrc = data.data['currentSrc']
                cb(currentSrc)
            })
        }

        let imgEle = window.document.getElementById('NextBtn')
        if (!imgEle) {
            loadBtnImage((currentSrc) => {
                console.log(`currentSrc: ${currentSrc}`)
                const videoW = 1920
                const videoH = 1080
                const videoEle = window.document.getElementById('CocosVideo')
                const scale = window.document.body.clientWidth / videoW
                const videoEleWidth = videoW * scale
                const videoEleHeight = videoH * scale
                const bodyWidth = window.document.body.clientWidth
                const bodyHeight = window.document.body.clientHeight
                const right = (bodyWidth - videoEleWidth) / 2
                const bottom = (bodyHeight - videoEleHeight) / 2

                const imgEle = window.document.createElement('img')
                imgEle.id = 'NextBtn'
                imgEle.src = currentSrc
                imgEle.style.position = 'absolute'
                imgEle.width = 88 * scale
                imgEle.height = 88 * scale
                imgEle.style.right = `${right + 35 * scale}px`
                imgEle.style.bottom = `${bottom + 35 * scale}px`
                window.document.body.appendChild(imgEle)
                imgEle.onclick = clickCallback
            })
        } else {
            imgEle.style.visibility = 'visible'
            imgEle.onclick = clickCallback
        }
    }

    hideHtmlNextBtn() {
        // let imgEle = window.document.getElementById('NextBtn')
        // window.document.body.removeChild(imgEle)
        GameManager.me.hideHtmlNextBtn()
    }

    showLogo() {
        let loadBtnImage = (cb: (src: string) => void) => {
            resources.load('product/PrimeFreeze/image/Logo_2', (err, data: ImageAsset) => {
                const currentSrc = data.data['currentSrc']
                cb(currentSrc)
            })
        }

        let imgEle = window.document.getElementById('Logo')
        if (!imgEle) {
            loadBtnImage((currentSrc) => {
                console.log(`currentSrc: ${currentSrc}`)
                const videoW = 1920
                const videoH = 1080
                const scale = window.document.body.clientWidth / videoW
                const videoEleWidth = videoW * scale
                const videoEleHeight = videoH * scale
                const bodyWidth = window.document.body.clientWidth
                const bodyHeight = window.document.body.clientHeight
                const right = (bodyWidth - videoEleWidth) / 2
                const top = (bodyHeight - videoEleHeight) / 2

                const imgEle = window.document.createElement('img')
                imgEle.id = 'Logo'
                imgEle.src = currentSrc
                imgEle.style.position = 'absolute'
                imgEle.width = 371 * scale
                imgEle.height = 69 * scale
                imgEle.style.right = `${right + 20 * scale}px`
                imgEle.style.top = `${top + 35 * scale}px`
                window.document.body.appendChild(imgEle)
            })
        } else {
            imgEle.style.visibility = 'visible'
        }
    }

    hideLogo() {
        let imgEle = window.document.getElementById('Logo')
        window.document.body.removeChild(imgEle)
    }

    showText() {
        let loadBtnImage = (cb: (src: string) => void) => {
            resources.load('product/PrimeFreeze/image/text_1', (err, data: ImageAsset) => {
                const currentSrc = data.data['currentSrc']
                cb(currentSrc)
            })
        }

        let imgEle = window.document.getElementById('VideoText')
        if (!imgEle) {
            loadBtnImage((currentSrc) => {
                console.log(`currentSrc: ${currentSrc}`)
                const videoW = 1920
                const videoH = 1080
                const scale = window.document.body.clientWidth / videoW
                const videoEleWidth = videoW * scale
                const videoEleHeight = videoH * scale
                const bodyWidth = window.document.body.clientWidth
                const bodyHeight = window.document.body.clientHeight
                const right = (bodyWidth - videoEleWidth) / 2
                const top = (bodyHeight - videoEleHeight) / 2

                const imgEle = window.document.createElement('img')
                imgEle.id = 'VideoText'
                imgEle.src = currentSrc
                imgEle.style.position = 'absolute'
                imgEle.width = 765 * scale
                imgEle.height = 190 * scale
                imgEle.style.right = `${(videoEleWidth - imgEle.width) / 2}px`
                imgEle.style.top = `${top + 350 * scale}px`
                window.document.body.appendChild(imgEle)
            })
        } else {
            imgEle.style.visibility = 'visible'
        }
    }

    hideText() {
        let imgEle = window.document.getElementById('VideoText')
        window.document.body.removeChild(imgEle)
    }

    hideVideo() {
        let ele = window.document.getElementById('CocosVideo')
        window.document.body.removeChild(ele)
    }
}
