import { classname } from '../util/Decorators'

@classname('MaouEvent')
export class MaouEvent {
    private static _pool: MaouEvent[] = []

    public type: string = null
    public target: object = null
    public data: object = null

    constructor(type: string, data?: object) {
        this.type = type
        this.data = data
    }

    /**
     * 
     */
    clone() {
        return MaouEvent.Create(this.constructor as { new (...args: any[]): any }, this.type, this.data)
    }

    /**
     *
     * @returns {string}
     */
    public toString(): string {
        return `${this.constructor['__NAME__']}(type=${this.type}, data=${JSON.stringify(this.data)})`
    }

    public dispose(): void {
        this.data = null
        this.target = null
        this.type = null
    }

    /**
     * 
     */
    public static Create<T extends MaouEvent>(EventClass: { new (...args: any[]): T }, type: string, data?: object): T {
        const eventPool = this._pool
        for (let i = 0; i < eventPool.length; i++) {
            if (eventPool[i] instanceof EventClass) {
                const evt = eventPool[i]
                evt.type = type
                evt.data = data
                evt.target = null
                eventPool.splice(i, 1)
                return evt as T
            }
        }

        return new EventClass(type, data)
    }

    /**
     * 
     */
    public static Recycle(event: MaouEvent) {
        const eventPool = this._pool
        // 
        if (eventPool.length >= 50) {
            return
        }
        event.dispose()
        eventPool.push(event)
    }
}
